import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController, ModalController, ToastController, ViewController } from 'ionic-angular';
import {DatePicker} from '@ionic-native/date-picker';
import { SoapService } from '../soap.service';
import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass } from '../../config';
import { DatePipe } from '@angular/common';


/**
 * Generated class for the SearchUangMukaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-search-uang-muka',
  providers: [SoapService],
  templateUrl: 'search-uang-muka.html',
})
export class SearchUangMukaPage {
  userdataTPK: any;
  isLoading: Boolean = true;
  uangMukaList: Array<any> = [];
  fakeUsers: Array<any> = new Array(5);  
  batasAtas = 1;
  batasBawah = 10;

  noSurat:any;
  perihal:any;
  noJkk:any;
  noPertanggungjawaban:any;
  tglPengajuan :any;


  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public soapService: SoapService,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController,
    public viewCtrl: ViewController,
    public datePicker: DatePicker,
    public datePipe: DatePipe
  ) {
    this.storage.get('userdataTPK').then(val => {      
      this.userdataTPK = val;            
        
    });  
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SearchUangMukaPage');
  }

  showDatePicker(type:number) {
    this.datePicker.show({
      date: new Date(),
      mode: 'date',
      androidTheme: this.datePicker.ANDROID_THEMES.THEME_HOLO_LIGHT
    }).then(date => {
        if (type == 1) {
          // this.startDate = this.datePipe.transform(date, 'dd-MM-yyyy'); 
        } else if (type == 2) {
          // this.endDate = this.datePipe.transform(date, 'dd-MM-yyyy');
        } else if (type == 3) {
          this.tglPengajuan = this.datePipe.transform(date, 'dd-MM-yyyy');
        }
      },
      err => console.log('Error occurred while getting date: ', err)
    );
  }

  getUangMukaList(type, functionName) {    
    if (type == 'first' && functionName == '') {
      this.isLoading = true;  
    }        
    this.soapService
      .post(api_base_url, 'eoffice_uang_muka_list', {
        fStream: JSON.stringify(
          {
            usernameEDI : api_user, 
            passwordEDI : api_pass,
            id_user:this.userdataTPK['data']['IDUSER'],           
            no_surat: this.noSurat,
            perihal: this.perihal,
            no_jkk: this.noJkk,
            no_pertanggungjawaban: this.noPertanggungjawaban,
            tgl_pengajuan : this.tglPengajuan,
            atas: this.batasAtas,
            bawah: this.batasBawah
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result)); 
        console.log(responData)       ;
        if (responData['rcmsg'] == "SUCCESS") {
          if (type == 'refresh' && functionName != '') {
            this.uangMukaList = [];
          }
          if (responData['data'].length > 0 ) {
            for (var i = 0; i < responData['data'].length; i++) {
              this.uangMukaList.push(responData['data'][i]);
            }
          }          
        } else {
          let toast = this.toastCtrl.create({
            message: 'Gagal mendapatkan data cuti, silahkan coba kembali.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }

        if (type == 'first' && functionName == '') {
          this.isLoading = false;  
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }        
        
      })
      .catch(error => {        
        let toast = this.toastCtrl.create({
          message: 'Gagal mendapatkan data cuti, periksa koneksi internet anda.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
      
        if (type == 'first' && functionName == '') {
          this.isLoading = false;  
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }

        this.isLoading = false;
      });
  }

  doInfinite(infiniteScroll) {
    if (this.uangMukaList.length >= 10) {
      this.batasAtas = this.batasBawah + 1;
      this.batasBawah = this.batasBawah + 10;
      this.getUangMukaList('infinite', infiniteScroll);
    } else {
      infiniteScroll.complete();
    }
  }

  doSearch() {    
    this.batasAtas = 1;
    this.batasBawah = 20;
    this.uangMukaList = []; 
    this.getUangMukaList('first', '');    
  }

  closeModal(){
    this.viewCtrl.dismiss();
 }

 clearField() {    
  this.noSurat= '';
  this.perihal= '';
  this.noJkk= '';
  this.noPertanggungjawaban= '';
  this.tglPengajuan = '';
  
}

}



